

.app{
  height: 100vh;
}

.sections{
  width: 100%;
  height: calc(100vh - 50px);
  position:relative;
  top: 50px;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory ;
  scrollbar-width:none;
}

.sections::-webkit-scrollbar{
  display: none;
}
  

.sections > *{
  width:100vw;
  height: calc(100vh - 50px);
  scroll-snap-align: start;
}

